 a {
  font-size: 12px;
  color: #5cb85c;
}

a:hover {
  font-size: 12px;
  color: #5cb85c;
}

.radio{
  text-align: center;
}

.card {
  margin-top: 5%;
  margin-bottom: 5%;
  margin-left: auto;
  margin-right: auto;
  max-width: 400px;
}

.header {
  text-align: center;
  margin-bottom: 5%;
}
.card p{
  display:flex;
  justify-content:center;
  font-size: 12px;
}

.card .text-alert {
  font-size: 10px;
  color: red !important;
  display:flex;
  justify-content:left;
  height: 15px;
}

.btn-block-language {
  display:flex;
  justify-content:center;
  margin-top: 5%;
  margin-bottom: 10%;
}
.btn-block-language .btn {
  width: 150px;
  height: 150px;
}

.btn-block-language .btn:first-child{
  margin-right: 20px;
}

.card-login .btn {
  margin-bottom: 10px;
}